/* media */
@media screen and (max-width: 780px) {

    .Navbar_hamburger__2I5nZ {
        display: block;
        cursor: pointer;
    }

    .Navbar_bar__3Vn-E {
        background-color: #120f0b;
        width: 20px;
        height: 3px;
        display: block;
        margin: 5px;
        transition: 0.3s ease-in-out;
    }

    /* adding active class to the bars  */
    .Navbar_hamburger__2I5nZ.Navbar_active__1xMnN .Navbar_bar__3Vn-E:nth-child(2) {
        opacity: 0;
    }

    .Navbar_hamburger__2I5nZ.Navbar_active__1xMnN .Navbar_bar__3Vn-E:nth-child(1) {
        -webkit-transform: translateY(8px) rotate(45deg);
                transform: translateY(8px) rotate(45deg);
    }

    .Navbar_hamburger__2I5nZ.Navbar_active__1xMnN .Navbar_bar__3Vn-E:nth-child(3) {
        -webkit-transform: translateY(-8px) rotate(-45deg);
                transform: translateY(-8px) rotate(-45deg);
    }


    /* navMenu */
    .Navbar_navMenu__1pSde {
        position: absolute;
        flex-direction: column;
        grid-gap: 0;
        gap: 0;
        top: 70px;
        left: -100%;
        text-align: start;
        width: 100%;
        transition: 0.7s ease-in-out;
        background-color: aliceblue;
        padding: 10px;
    }

    .Navbar_navMenu__1pSde.Navbar_active__1xMnN {
        left: 0;
    }

    .Navbar_navMenu__1pSde li {
        margin: 16px 0;
    }
}
